import TripPointView from '../view/trip-point-view.js';
import { render } from '../framework/render.js';


/**
 *     TripPointView
 */

export default class PointPresenter {
  #pointPresenterContainer = null;
  #destination = null;
  #offerTripPoint = null;
  #offersModel = null;
  #onEditClick = null;
  #component = null;

  /**
   *     ContentPresenter
   * @param {object} pointPresenterContainer      
   * @param {object} destination      
   * @param {object} offerTripPoint      
   * @param {object} onEditClick        
   */

  constructor({
    pointPresenterContainer,
    destination,
    offerTripPoint,
    offersModel,
    onEditClick
  }) {
    this.#pointPresenterContainer = pointPresenterContainer;
    this.#destination = destination;
    this.#offerTripPoint = offerTripPoint;
    this.#offersModel = offersModel;
    this.#onEditClick = onEditClick;
  }

  /**
   *   
   */

  init(tripPoint) {
    this.#setComponent(tripPoint);
    this.#renderTripPoint();
  }

  /**
   *    TripPointView
   */

  #renderTripPoint() {
    render(this.#component, this.#pointPresenterContainer);
  }

  /**
   *     TripPointView
   * @param {object} tripPoint   
   * @param {object} destination     
   * @param {object} offers     
   */

  #setComponent(tripPoint) {
    this.#component = new TripPointView({
      tripPoint: tripPoint,
      destination: this.#destination,
      offer: this.#offerTripPoint,
      offersModel: this.offersModel,
      onEditClick: this.#onEditClick
    });
  }

  /**
   *     TripPointView
   */

  get component() {
    return this.#component;
  }

}
